:root {
  --bg: #F2F0F1;
  --color-contnet: #FF8A00;
}

@font-face {
  font-family: HelveticaNeueCyr;
  font-weight: 300;
  font-display: swap;
  src: local("HelveticaNeueCyr"), url("../fonts/HelveticaNeueCyr-Light.woff2") format("woff2"), url("../fonts/HelveticaNeueCyr-Light.woff") format("woff");
}
@font-face {
  font-family: HelveticaNeueCyr;
  font-weight: 400;
  font-display: swap;
  src: local("HelveticaNeueCyr"), url("../fonts/HelveticaNeueCyr-Roman.woff2") format("woff2"), url("../fonts/HelveticaNeueCyr-Roman.woff") format("woff");
}
@font-face {
  font-family: HelveticaNeueCyr;
  font-weight: 700;
  font-display: swap;
  src: local("HelveticaNeueCyr"), url("../fonts/HelveticaNeueCyr-Bold.woff2") format("woff2"), url("../fonts/HelveticaNeueCyr-Bold.woff") format("woff");
}
@font-face {
  font-family: Comfortaa;
  font-weight: 400;
  font-display: swap;
  src: local("Comfortaa"), url("../fonts/Comfortaa-Regular.woff2") format("woff2"), url("../fonts/Comfortaa-Regular.woff") format("woff");
}
html {
  position: relative;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.body {
  min-width: 320px;
  min-height: 100vh;
  font-family: HelveticaNeueCyr, sans-serif;
  font-size: 20px;
  font-weight: 300;
}

h1, h2 {
  font-size: 50px;
  font-family: Comfortaa, sans-serif;
  font-weight: 400;
  margin: 0;
  padding: 0;
}

h3 {
  font-family: HelveticaNeueCyr, sans-serif;
  font-weight: 400;
  font-size: 30px;
  text-align: left;
  margin: 0;
  padding: 0;
}

ul, ol, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  white-space: pre-line;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
}

.container {
  max-width: 1300px;
  padding: 0 60px;
  margin: 0 auto;
}
@media (max-width: 1200px) {
  .container {
    padding: 0 72px;
  }
}
@media (max-width: 1000px) {
  .container {
    padding: 0 34px;
  }
}
@media (max-width: 640px) {
  .container {
    padding: 0 25px;
  }
}

section {
  padding: 90px 0;
}
@media (max-width: 1200px) {
  section {
    padding: 70px 0;
  }
}
@media (max-width: 1000px) {
  section {
    padding: 60px 0;
  }
}
@media (max-width: 640px) {
  section {
    padding: 30px 0;
  }
}

.btn {
  position: absolute;
  padding: 0;
  width: 180px;
  height: 155px;
  border: none;
  background-color: transparent;
  font-family: HelveticaNeueCyr;
  font-style: normal;
  font-weight: 550;
  font-size: 14px;
  line-height: 22px;
  text-align: center;
  text-transform: uppercase;
  color: var(--color-contnet);
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: filter 0.5s;
}
@media (max-width: 1200px) {
  .btn {
    font-size: 12px;
    line-height: 18px;
    width: 148px;
    height: 126px;
  }
}
@media (max-width: 640px) {
  .btn {
    font-weight: 550;
    font-size: 8px;
    line-height: 12px;
    width: 84px;
    height: 78px;
  }
}
.btn__img {
  transition: filter 0.3s;
}
@media (max-width: 1200px) {
  .btn__img {
    width: 83px;
    height: 80px;
  }
}
@media (max-width: 640px) {
  .btn__img {
    width: 51px;
    height: 50px;
  }
}
.btn:hover, .btn:active {
  filter: drop-shadow(0px 4px 6px rgba(0, 0, 0, 0.25));
}
.btn:hover .btn__img, .btn:active .btn__img {
  filter: drop-shadow(0px -4px 6px var(--color-contnet));
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 21px;
  margin-bottom: 27px;
}

.menu-burger {
  position: relative;
  widows: 40px;
  height: 21px;
}
.menu-burger__svg:hover {
  opacity: 0.5;
}
.menu-burger__svg:active {
  opacity: 1;
}
.menu-burger__btn {
  border: none;
  background-color: transparent;
}
.menu-burger__svg {
  margin-bottom: 15px;
}
.menu-burger__svg_close {
  display: none;
}
.menu-burger__nav {
  display: none;
  position: absolute;
  top: 45px;
  left: 0;
  width: 394px;
  height: 421px;
  padding: 27px 0 0 0;
  z-index: 1;
}
@media (max-width: 1200px) {
  .menu-burger__nav {
    width: 349px;
    height: 347px;
  }
}
@media (max-width: 1000px) {
  .menu-burger__nav {
    width: 349px;
    height: 415px;
  }
}
@media (max-width: 640px) {
  .menu-burger__nav {
    padding: 0;
    left: -20px;
    width: 297px;
    height: 326px;
  }
}
.menu-burger__nav:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  background-image: url("./../img/polygon.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: block;
  width: 52px;
  height: 30px;
  z-index: -1;
}
@media (max-width: 640px) {
  .menu-burger__nav:before {
    display: none;
  }
}
.menu-burger__items {
  width: 100%;
  height: 394px;
  padding: 56px 40px 0 20px;
  background: var(--bg);
  border-bottom: 10px solid var(--color-contnet);
}
@media (max-width: 1200px) {
  .menu-burger__items {
    padding: 40px 20px 0;
    height: auto;
  }
}
@media (max-width: 1000px) {
  .menu-burger__items {
    padding: 40px 20px 0;
    height: 388px;
  }
}
@media (max-width: 640px) {
  .menu-burger__items {
    padding: 0;
    width: 100%;
    height: 326px;
  }
}
.menu-burger__item {
  border-bottom: 1px solid var(--color-contnet);
  padding: 10px 0 20px 20px;
}
.menu-burger__item:nth-child(5) {
  border-bottom: none;
}
.menu-burger__item:last-child {
  border-bottom: none;
}
.menu-burger__link {
  font-family: HelveticaNeueCyr;
  font-weight: 550;
  font-size: 20px;
  line-height: 30px;
  color: #282626;
}
@media (max-width: 1200px) {
  .menu-burger__link {
    font-size: 18px;
    line-height: 24px;
  }
}
@media (max-width: 640px) {
  .menu-burger__link {
    font-size: 14px;
    line-height: 20px;
  }
}

@media (max-width: 1200px) {
  .logo__img {
    width: 300px;
    height: 59px;
  }
}
@media (max-width: 640px) {
  .logo__img {
    width: 161px;
    height: 32px;
  }
}

.contacts {
  font-family: HelveticaNeueCyr, Helvetica, Arial, sans-serif;
  font-weight: 550;
  font-size: 20px;
  line-height: 30px;
  color: #282626;
  background-image: url("./../img/call.svg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 24px;
  padding-left: 38px;
}
@media (max-width: 840px) {
  .contacts {
    display: none;
  }
}

.menu-burger__contacts {
  display: none;
}
@media (max-width: 840px) {
  .menu-burger__contacts {
    display: block;
    font-size: 18px;
    line-height: 24px;
    margin-left: 17px;
    background-size: 24px;
    padding-top: 10px;
    padding-bottom: 0;
  }
}
@media (max-width: 640px) {
  .menu-burger__contacts {
    font-size: 14px;
    line-height: 20px;
    padding-top: 0;
  }
}

.banner {
  padding-top: 0;
}
.banner__container {
  position: relative;
  background-image: url("./../img/bg-banner-sara.png");
  background-image: -webkit-image-set(url("./../img/bg-banner-sara.avif") 1x, url("./../img/bg-banner-sara.webp") 1x, url("./../img/bg-banner-sara.png") 1x);
  background-image: image-set(url("./../img/bg-banner-sara.avif") 1x, url("./../img/bg-banner-sara.webp") 1x, url("./../img/bg-banner-sara.png") 1x);
  background-repeat: no-repeat;
  background-position: center center;
  height: 580px;
  display: flex;
  justify-content: space-between;
  z-index: -1;
}
@media (max-width: 1200px) {
  .banner__container {
    height: 450px;
    background-size: 416px;
  }
}
@media (max-width: 840px) {
  .banner__container {
    height: 498px;
    flex-wrap: wrap;
    justify-content: end;
    background-position: center bottom;
  }
}
@media (max-width: 640px) {
  .banner__container {
    height: 536px;
    background-position: center top 82px;
    background-size: 270px;
    flex-direction: column;
    justify-content: start;
    gap: 315px;
  }
}
@media (max-width: 516px) {
  .banner__container {
    gap: 290px;
  }
}
.banner__title {
  display: inline-block;
  padding-top: 137px;
  font-family: Comfortaa;
  font-style: normal;
  font-weight: 300;
  font-size: 40px;
  line-height: 60px;
  width: 480px;
}
@media (max-width: 1200px) {
  .banner__title {
    padding-top: 69px;
    font-weight: 300;
    font-size: 30px;
    line-height: 44px;
    width: 300px;
  }
}
@media (max-width: 840px) {
  .banner__title {
    font-size: 24px;
    line-height: 30px;
    padding-top: 0;
    width: 100%;
  }
}
@media (max-width: 640px) {
  .banner__title {
    font-size: 18px;
    line-height: 26px;
  }
}
.banner__title_color {
  font-size: 60px;
  font-weight: normal;
  color: var(--color-contnet);
}
@media (max-width: 1200px) {
  .banner__title_color {
    font-size: 44px;
    line-height: 54px;
  }
}
@media (max-width: 840px) {
  .banner__title_color {
    font-size: 40px;
    line-height: 44px;
  }
}
@media (max-width: 640px) {
  .banner__title_color {
    font-size: 26px;
    line-height: 44px;
  }
}
.banner__btn {
  left: 100px;
  bottom: 25px;
}
@media (max-width: 1200px) {
  .banner__btn {
    bottom: 60px;
  }
}
@media (max-width: 840px) {
  .banner__btn {
    left: 30px;
    bottom: 171px;
  }
}
@media (max-width: 640px) {
  .banner__btn {
    bottom: 0;
    left: calc(50% - 42px);
  }
}
.banner__items {
  padding-top: 100px;
  display: flex;
  flex-direction: column;
  gap: 30px;
  margin-right: 100px;
}
@media (max-width: 1200px) {
  .banner__items {
    margin-right: 0;
    padding-top: 32px;
    gap: 20px;
  }
}
@media (max-width: 840px) {
  .banner__items {
    gap: 15px;
    padding-top: 83px;
  }
}
@media (max-width: 640px) {
  .banner__items {
    flex-direction: row;
    padding-top: 0;
    justify-content: center;
    gap: 5px;
  }
}
.banner__item {
  width: 180px;
  height: 80px;
  display: flex;
  align-items: center;
  gap: 20px;
  cursor: pointer;
  border: 1px solid transparent;
}
@media (max-width: 840px) {
  .banner__item {
    width: 140px;
    height: 60px;
  }
}
@media (max-width: 640px) {
  .banner__item {
    width: 60px;
    height: 85px;
    flex-direction: column;
    gap: 5px;
    justify-content: space-between;
  }
}
.banner__img {
  z-index: -1;
}
@media (max-width: 840px) {
  .banner__img {
    width: 60px;
    height: 60px;
  }
}
@media (max-width: 640px) {
  .banner__img {
    width: 60px;
    height: 60px;
  }
}
.banner__item:hover {
  border: 1px solid var(--color-contnet);
}
.banner__item:active {
  border: 1px solid var(--color-contnet);
}
.banner__item:active .banner__name {
  color: var(--color-contnet);
}
.banner__name {
  font-family: HelveticaNeueCyr;
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 26px;
  color: #282626;
}
@media (max-width: 1200px) {
  .banner__name {
    font-size: 18px;
    line-height: 24px;
  }
}
@media (max-width: 840px) {
  .banner__name {
    font-size: 14px;
    line-height: 20px;
  }
}
.banner__activ {
  display: none;
}

.present {
  background-color: var(--bg);
}

.present__container {
  position: relative;
  background-image: url("./../img/bg-dog-about.jpg");
  background-image: -webkit-image-set(url("./../img/bg-dog-about.avif") 1x, url("./../img/bg-dog-about.webp") 1x, url("./../img/bg-dog-about.jpg") 1x);
  background-image: image-set(url("./../img/bg-dog-about.avif") 1x, url("./../img/bg-dog-about.webp") 1x, url("./../img/bg-dog-about.jpg") 1x);
  background-repeat: no-repeat;
  background-size: 580px 330px;
  background-position: right;
}

.present__title {
  text-align: left;
  color: #282626;
  margin-bottom: 33px;
}

.present__discription {
  font-family: HelveticaNeueCyr;
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 30px;
  color: #282626;
}

.present__news {
  position: absolute;
  right: 19px;
  bottom: 35px;
  font-family: HelveticaNeueCyr;
  font-style: normal;
  font-weight: 550;
  font-size: 20px;
  line-height: 26px;
  color: var(--color-contnet);
  background-image: url("./../img/bi_arrow-right.png");
  background-image: -webkit-image-set(url("./../img/bi_arrow-right.avif") 1x, url("./../img/bi_arrow-right.webp") 1x, url("./../img/bi_arrow-right.png") 1x);
  background-image: image-set(url("./../img/bi_arrow-right.avif") 1x, url("./../img/bi_arrow-right.webp") 1x, url("./../img/bi_arrow-right.png") 1x);
  background-repeat: no-repeat;
  background-size: 29px;
  background-position: right;
  padding-right: 48px;
}

.my-project__title {
  line-height: 60px;
  margin-bottom: 50px;
}

.my-project__grid-items {
  display: grid;
  grid-template-columns: 680px 380px;
  justify-content: space-between;
}

.my-project__items {
  display: grid;
  grid-template: repeat(4, 1fr)/100%;
  gap: 30px;
}

.my-project__item {
  display: grid;
  grid-template-columns: 280px auto;
  grid-template-rows: repeat(3, auto);
  grid-template-areas: "img content1" "img content2" "img content3";
  gap: 0 18px;
}

.my-project__picture {
  grid-area: img;
}

.my-project__name {
  font-weight: 550;
  font-size: 30px;
  line-height: 40px;
  grid-area: content1;
}

.my-project__subscription {
