.modal {    
    display: none;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.8)    
}

.modal__close {
    position: absolute;
    top: 20px;
    right: 25px;
}

.form__input_modal:invalid {
    outline:  1px solid red;
}